import Vue from 'vue'
import VueRouter from 'vue-router'
import html from '@/view/html'
import css from '@/view/css'
import js from '@/view/js'
import node from '@/view/node'
import vuecli from '@/view/vuecli'
import xcx from '@/view/xcx'
import python from '@/view/python'
import php from '@/view/php'
import java from '@/view/java'
import Hover from '@/view/hover'
import Hoverto from '@/view/hoverto'
import Tanxing from '@/view/tanxing'
import Time from '@/view/time'
let Home = (resolve) => {
  return require.ensure([], () => {
    resolve(require('@/components/home'))
  })
}
let Document = (resolve) => {
  return require.ensure([], () => {
    resolve(require('@/components/document'))
  }, 'about')
}
let Transition = (resolve) => {
  return require.ensure([], () => {
    resolve(require('@/components/transition'))
  })
}
let About = (resolve) => {
  return require.ensure([], () => {
    resolve(require('@/components/about'))
  })
}
Vue.use(VueRouter)
let router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Home,
      meta: {
        index: 0,
        title: '首页'
      }
    },
    {
      path: '/document',
      component: Document,
      children: [
        {
          path: '',
          component: html,
          meta: {
            index: 1,
            title: '随手笔记'
          }
        },
        {
          path: 'css',
          component: css
        },
        {
          path: 'js',
          component: js
        },
        {
          path: 'node',
          component: node
        },
        {
          path: 'vuecli',
          component: vuecli
        },
        {
          path: 'xcx',
          component: xcx
        },
        {
          path: 'python',
          component: python
        },
        {
          path: 'php',
          component: php
        },
        {
          path: 'java',
          component: java
        }
      ]
    },
    {
      path: '/about',
      component: About,
      meta: {
        index: 3,
        title: '关于'
      }
    },
    {
      path: '/transition',
      component: Transition,
      children: [
        {
          path: '',
          component: Hover,
          meta: {
            index: 2,
            title: '随手效果'
          }
        },
        {
          path: 'hoverto',
          component: Hoverto
        },
        {
          path: 'tanxing',
          component: Tanxing
        },
        {
          path: 'time',
          component: Time
        }
      ]
    },
    {
      path: '*',
      redirect: '/home'
    }
  ]
})
router.afterEach((to, from) => {
  if (to.meta.title) {
    window.document.title = to.meta.title
  } else {
    window.document.title = '随手笔记'
  }
})
export default router



