<template>
<div class="index">
  <h2 class='transtion-h2'>1.手机效果左右拖动</h2>
  <div class="warp">
    <ul id="warp-in" @mousedown="hoverDown()">
      <li :key="index" v-for="(item,index) in imgSrc1">
        <img :src="item.src"  draggable="false"/>
      </li>
    </ul>
  </div>
</div>
</template>
<script>
let imgSrc1 = [
  {
    src: require('../assets/img/0 (1).jpg')
  },
  {
    src: require('../assets/img/0 (2).jpg')
  },
  {
    src: require('../assets/img/0 (3).jpg')
  },
  {
    src: require('../assets/img/0 (4).jpg')
  },
  {
    src: require('../assets/img/0 (5).jpg')
  },
  {
    src: require('../assets/img/0 (6).jpg')
  },
  {
    src: require('../assets/img/0 (7).jpg')
  },
  {
    src: require('../assets/img/0 (8).jpg')
  },
  {
    src: require('../assets/img/0 (9).jpg')
  },
  {
    src: require('../assets/img/0 (10).jpg')
  }
]
export default {
  name: 'home',
  data () {
    return {
      imgSrc1: imgSrc1,
      Inow: 0,
      timer: null
    }
  },
  methods: {
    hoverDown (event) {
      console.log(event)
      var oUl = document.getElementById('warp-in')
      var oLi = oUl.getElementsByTagName('li')
      var dixI = 0
      clearInterval(this.timer)
      var ev = event || window.event
      var dix = ev.clientX - oUl.offsetLeft
      dixI = ev.clientX
      var _this = this
      document.onmousemove = function (event) {
        var ev = event || window.event
        oUl.style.left = ev.clientX - dix + 'px'
      }
      document.onmouseup = function (event) {
        var ev = event || window.event
        document.onmousemove = null
        document.onmouseup = null
        console.log(_this.Inow)
        if (dixI > ev.clientX) {
          if (_this.Inow < oLi.length - 1) {
            _this.Inow = _this.Inow + 1
          }
          _this.starNow(-_this.Inow * oLi[0].offsetWidth, oUl)
        } else {
          if (_this.Inow !== 0) {
            _this.Inow = _this.Inow - 1
          }
          _this.starNow(-_this.Inow * oLi[0].offsetWidth, oUl)
        }
      }
      return false
    },
    starNow (iTarget, oUl) {
      var ipeed = 0
      clearInterval(this.timer)
      this.timer = setInterval(function () {
        ipeed += (iTarget - oUl.offsetLeft) / 6
        ipeed *= 0.75
        if (Math.abs(ipeed) < 1 && Math.abs(iTarget - oUl.offsetLeft) < 1) {
          clearInterval(this.timer)
          ipeed = 0
          oUl.style.left = iTarget + 'px'
        } else {
          oUl.style.left = oUl.offsetLeft + ipeed + 'px'
        }
      }, 30)
    }
  }
}

</script>
<style>
</style>



