<template>
<div class="index">
  <h2 class='transtion-h2'>1.滑过显示</h2>
  <ul id="ul">
    <li :key="index" v-for='(numbId, index) in imgSto' @mouseover="houerYu(numbId, index)" @mouseout="outYu()">
      <img :class="[backgroundImge == index ? 'transition-show' : '']" :src='numbId.src' />
      <span :class="[backgroundImge == index ? 'transition-show' : '']">{{numbId.title}}</span>
    </li>
  </ul>
</div>
</template>
<script>
let imgSto = [
  {
    src: require('@/assets/img/0 (1).jpg'),
    title: '你真帅气'
  },
  {
    src: require('../assets/img/0 (2).jpg'),
    title: '你真可爱'
  },
  {
    src: require('../assets/img/0 (3).jpg'),
    title: '你真漂亮'
  },
  {
    src: require('../assets/img/0 (4).jpg'),
    title: '你真淘气'
  },
  {
    src: require('../assets/img/0 (5).jpg'),
    title: '你真高'
  },
  {
    src: require('../assets/img/0 (6).jpg'),
    title: '你真迷人'
  },
  {
    src: require('../assets/img/0 (7).jpg'),
    title: '你真笨'
  },
  {
    src: require('../assets/img/0 (8).jpg'),
    title: '你真坏'
  },
  {
    src: require('../assets/img/0 (9).jpg'),
    title: '你真胖'
  }
]
export default {
  name: 'home',
  data () {
    return {
      imgSto: imgSto,
      backgroundImge: -1
    }
  },
  methods: {
    houerYu (numbId, index) {
      this.backgroundImge = index
    },
    outYu () {
      this.backgroundImge = -1
    }
  }
}

</script>
<style>
</style>



