<template>
<div class="index">
  <h2 class='transtion-h2'>1.canvas时钟</h2>
  <canvas id="cl" width="400" height="400"></canvas>
</div>
</template>
<script>
export default {
  name: 'home',
  data () {
    return {
    }
  },
  mounted: function () {
    var oc = document.getElementById('cl')
    var ocg = oc.getContext('2d')
    function toDraw () {
      var x = 200
      var y = 200
      var r = 150
      var oDate = new Date()
      var oHours = oDate.getHours()
      var oMin = oDate.getMinutes()
      var oSon = oDate.getSeconds()
      var oHours1 = (-90 + oHours * 30 + oMin / 2) * Math.PI / 180
      var oMin1 = (-90 + oMin * 6) * Math.PI / 180
      var oSon1 = (-90 + oSon * 6) * Math.PI / 180
      ocg.clearRect(0, 0, oc.width, oc.height)
      ocg.beginPath()
      for (var i = 0; i < 60; i++) {
        ocg.moveTo(x, y)
        ocg.arc(x, y, r, 6 * i * Math.PI / 180, 6 * (i + 1) * Math.PI / 180, false)
      }
      ocg.closePath()
      ocg.stroke()
      ocg.fillStyle = '#AAAAAA'
      ocg.beginPath()
      ocg.moveTo(x, y)
      ocg.arc(x, y, r * 19 / 20, 0, 360 * i * Math.PI / 180, false)
      ocg.closePath()
      ocg.fill()
      ocg.lineWidth = 3
      ocg.beginPath()
      for (var j = 0; j < 12; j++) {
        ocg.moveTo(x, y)
        ocg.arc(x, y, r, 30 * j * Math.PI / 180, 30 * (j + 1) * Math.PI / 180, false)
      }
      ocg.closePath()
      ocg.stroke()
      ocg.fillStyle = '#fff'
      ocg.beginPath()
      ocg.moveTo(x, y)
      ocg.arc(x, y, r * 18 / 20, 0, 360 * Math.PI / 180, false)
      ocg.closePath()
      ocg.fill()
      ocg.lineWidth = 5
      ocg.beginPath()
      ocg.moveTo(x, y)
      ocg.arc(x, y, r * 10 / 20, oHours1, oHours1, false)
      ocg.closePath()
      ocg.stroke()
      ocg.lineWidth = 3
      ocg.beginPath()
      ocg.moveTo(x, y)
      ocg.arc(x, y, r * 14 / 20, oMin1, oMin1, false)
      ocg.closePath()
      ocg.stroke()
      ocg.lineWidth = 2
      ocg.beginPath()
      ocg.moveTo(x, y)
      ocg.arc(x, y, r * 17 / 20, oSon1, oSon1, false)
      ocg.closePath()
      ocg.stroke()
      ocg.fillStyle = '#f00'
      ocg.beginPath()
      ocg.moveTo(x, y)
      ocg.arc(x, y, r * 1 / 20, 0, 360 * Math.PI / 180, false)
      ocg.closePath()
      ocg.fill()
    }
    setInterval(function () {
      toDraw()
    }, 1000)
  }
}

</script>
<style>
</style>



