<template>
<div id="app">
  <ul class="index-nav">
    <li>
      <router-link to='/' exact >首页</router-link>
    </li>
    <li>
      <router-link to='/document'>随手笔记</router-link>
    </li>
    <li>
      <router-link to='/transition'>随手效果</router-link>
    </li>
    <li>
      <router-link to='/about'>关于</router-link>
    </li>
  </ul>
  <transition :name="names">
    <router-view class="center"></router-view>
  </transition>
</div>
</template>

<script>
export default {
  name: 'App',
  watch: {
    $route (to, form) {
      if (to.meta.index > form.meta.index) {
        this.names = 'left'
      } else {
        this.names = 'right'
      }
    }
  },
  data () {
    return {
      names: 'left'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.left-enter{
  transform: translateX(100%);
}
.left-enter-active,.left-leave-active{
  transition:1s;
}

.left-leave-to{
  transform: translateX(-100%);
}
.right-enter{
  transform: translateX(-100%);
}
.right-enter-active,.right-leave-active{
  transition:1s;
}

.right-leave-to{
  transform: translateX(100%);
}
</style>



