<template>
<div class="html-main">
  <div>
    <h3>1.node使用中遇到的一些问题</h3>
    <p>node搭建服务端</p>
    <p>1.学习的一些资料，与最新的一些版本会有一些被弃用的方法需要注意，需要查看官方的文档</p>
    <p>2.mongoose.connect('mongodb://localhost:27017/blog',{ useNewUrlParser: true }，function(){}（需要添加{ useNewUrlParser: true }不然会报错的）</p>
    <p>3.有一个问题困扰了我好一阵子，对于初学者来说，schema去关联另一schema，ref去对应，始终获取不到，最后的问题是自己错误的把ref对应了当前的字段所以没有获取到，应该是对应你想要获取的字段即可</p>
  </div>
  <p style="width:100%;height:140px;"></p>
</div>
</template>
<script>
export default {
  name: 'home'
}

</script>
