<template>
<div class="html-main">
  <div>
    <h3>1.浏览器兼容问题</h3>
    <p>高度塌陷问题</p>
    <p>给元素添加content: ".";display: block;height: 0px;clear: both;visibility: hidden;</p>
  </div>
  <div>
    <h3>2.浏览器兼容问题</h3>
    <p>内联元素和块级元素</p>
    <p>内联元素默认写不上高度和宽度，需要添加display:block;转成块级元素</p>
  </div>
  <p style="width:100%;height:140px;"></p>
</div>
</template>
<script>
export default {
  name: 'home'
}

</script>
<style>
</style>



